:root {
  /* declare CSS variables with `--` syntax */
  --dark: #13293d;
  --light: #fff;
  --navlink-color: rgb(0, 76, 255);
  --pad20: 20px;
}
html {
  scroll-behavior: smooth;
}
header,
footer {
  width: 100%;
  height: auto;
  padding: var(--pad20);
  color: #fff;
  text-shadow: 1px 1px #333;
}
header {
  background-image: linear-gradient(to bottom, #368727, transparent);
  padding-bottom: 5em;
  display: flex;
  justify-content: space-around;
}
.intro {
  display: block;
  max-width: 600px;
  margin: 2em 5em;
}
.intro p {
  padding: var(--pad20) 0;
  margin: auto;
  text-align: left;
}
nav {
  width: 35%;
  /* float: left; */
  justify-content: flex-end;
  max-width: 600px;
}
nav ul li {
  display: inline-block;
  margin: 0 10px;
  text-shadow: 1px 1px 5px #999;
}
a {
  margin: 5px;
  display: block;
  text-decoration: none;
  color: var(--navlink-color);
}
h1 a {
  color: #fff;
}
h1 a:hover, h1 a:active {
  text-decoration: none;
  cursor: pointer;
  color: #fff;
}
nav ul {
  padding-left: 0;
}
nav a {
  padding: 10px;
}
a:focus {
  color: purple;
}
a:hover, a:active {
  color: orange;
  text-decoration: underline;
}
nav a:hover, nav a.active {
  color: orange;
  text-decoration: underline;
  padding: 10px;
  border: solid 1px #ccc;
  border-radius: 5px;
}
h1,
h2 {
  text-align: center;
  display: block;
  font-size: 4em;
}
img {
  display: block;
  /* height: 50px;
    width: 50px; */
  margin: auto;
}
.bio {
  max-width: 300px;
  border-radius: 10px;
  max-height: 350px;
}
aside {
  /* float: left; */
  margin-right: 15px;
  width: 20%;
}
aside ul {
  max-width: 215px;
  padding-left: var(--pad20);
}
aside a {
  margin: 15px 0;
}
/* flexbox code is going in here */
.container {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-evenly; */
  align-items: flex-start;
  margin-bottom: 2em;
  color: #333;
}
.projects {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
section {
  color: #000;
  padding: var(--pad20) 0;
}
ul {
  padding-left: 40px;
  font-size: 20px;
  text-align: left;
}
p {
  text-align: left;
  font-size: 18px;
  line-height: 1.25;
}
.card {
  width: 50%;
  max-width: 500px;
  margin: 1em;
  border: 1px solid #333;
  border-radius: 5px;
  box-shadow: 5px 5px 2px #888888;
}
.card:hover {
  box-shadow: 0 0 5px;
}
.card img {
  width: 100%;
  max-height: 320px;
}
.card h4 {
  font-size: 2em;
  color: #666;
  margin: 20px;
  text-align: center;
}
.card p {
  margin: 25px;
}
a.button,
button.submit {
  display: block;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  width: 50%;
  margin: auto;
  color: #fff;
  background-color: #368727;
  border-radius: 6px;
  outline: none;
}
a.button:hover {
  /* color: #888888; */
  box-shadow: 1px 1px 4px #666;
}
#featured {
  width: 95%;
}
#featured img {
  max-height: 400px;
  max-width: 650px;
}
#featured p {
  text-align: center;
}
.contact h2,
.contact p,
.contact ul {
  margin: 1em;
}
.contact h2 {
  text-align: left;
  margin-left: 0.25em;
  margin-top: 0;
}
.contact ul {
  padding-left: 0.75em;
}
.contact ul li {
  list-style-type: disc;
  margin: 5px;
}
.contact ul a {
  margin: 0;
}
form {
  color: #8597a3;
  position: relative;
  background-color: #fafafe;
  border-radius: 10px;
  margin: 40px;
  padding: 25px 20px 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 500px;
  box-sizing: border-box;
}
input {
  border: 0;
  padding: 0;
  background-color: transparent;
  border-bottom: 2px solid #eee;
  font: inherit;
  font-size: 14px;
  line-height: 30px;
  width: 100%;
  margin: 1em auto;
}
.submit {
  margin: 2em auto;
}
footer {
  background-color: #368727;
}
footer h4 {
  font-size: 18px;
}

/* CSS media queries code goes at the bottom */
@media screen and (max-width: 992px) {
  header {
    flex-direction: column;
  }
  .bio {
    margin: auto;
  }
  div.intro {
    max-width: none;
    margin: 1em;
  }
  nav {
    width: 100%;
    justify-content: space-around;
    max-width: none;
  }
  nav ul {
    text-align: center;
  }
  nav ul li {
    margin: 0;
    width: 24%;
  }
  aside {
    width: auto;
    margin: auto;
  }
  aside ul {
    display: inline-block;
    max-width: none;
  }
  aside ul li {
    display: inline-block;
    margin: 0.5em;
  }
  .container {
    flex-direction: column;
    align-items: center;
  }
  .card {
    width: 95%;
    margin: 1em auto;
  }
  form {
    margin: 2em auto;
    width: 90%;
  }
}

/* Large smartphones */
@media screen and (max-width: 768px) {
  nav {
    text-align: center;
  }
}
